import { Component, OnInit } from '@angular/core';
import {Location} from "@angular/common";
import {Storage} from "@ionic/storage";
import {CommonService} from "../../services/common.service";
import {EventProviderService} from "../../services/event-provider.service";
import {AnalyticsService} from "../../services/analytics.service";
import {Platform} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {TranslateService} from "@ngx-translate/core";
import {ApiService} from "../../services/api.service";

@Component({
  selector: 'app-call-studio',
  templateUrl: './call-studio.page.html',
  styleUrls: ['./call-studio.page.scss'],
})
export class CallStudioPage implements OnInit {

  ch_id: any;
  theme: any;
  address: any;
  phone: any;
  email: any;
  enable_home: any;
  start_time_analytics: any;
  page_name: any;
  custom_name: any;
  user: any;
  calls: any;
  page_in_home_tabs: any = false;
  page: any;

  constructor(
      private location: Location,
      private storage: Storage,
      private common: CommonService,
      private eventProvider: EventProviderService,
      private analytics: AnalyticsService,
      private platform: Platform,
      private device: Device,
      private translate: TranslateService,
      private api: ApiService,
  ) {
    // chekcing if page is opened in home tabs or simple
    let current_url = new URL(window.location.href);
    let page_href = current_url.pathname;
    if (page_href.includes('home-tabs')){
      this.page_in_home_tabs = true;
    } else {
      this.page_in_home_tabs = false;
    }
  }

  ngOnInit() {
  }

  ionViewWillEnter(){
    this.getTheme();
  }

  getTheme() {
    // get theme from local storage
    this.storage.get('theme').then(res => {
      if (res) {
        this.ch_id = res.ch_id;

        // preparing data to call API through service to get latest data
        const body = JSON.stringify({id: this.ch_id});
        this.common.getTheme(body).subscribe(async (theme: any) => {
          this.theme = theme.church;
          // getting app_pages to get latest data
          if (theme && theme.status) {
            // Filtering out CONTACT US page from app pages
            let page = theme.apppages.find(x => x.activated === 'true' && x.name === "Call Studio");
            let myPage = page;
            this.page = myPage;
            this.page_name = myPage.name;
            this.custom_name = myPage.title;
            this.getCalls();
          }
        });
      }
    });
  }

  getCalls(){
    this.api.getCallStudio(this.ch_id).subscribe( (res: any) => {
      if (res && res.status === true){
        this.calls = res.groups;
      }
    })
  }


  gotobackPage() {
    this.location.back();
  }

}
